@import '../../style/variables.scss';

.app {
    &__header {
        display: flex;
        justify-content: space-between;
    }
    &__title {
        font-weight: bold;
        font-size: 28px;
        line-height: 37px;
        span {
            color: $main-color;
        }
    }
    &__menu {
        &-lists {
            display: flex;
            justify-content: space-between;
            font-weight: bold;
            font-size: 24px;
            line-height: 32px;
        }
        &-list {
            margin: 0 8px;
        }
        
        &-link:hover {
                transition:ease-in .4s;
                transform: scale(1.1);          
        } 
    }
}
@media (max-width: 769px) {
    .app {
        &__title {
            font-size: 36px;
        }
        &__menu {
            &-lists {
                font-size: 30px;
            }
        }
    }
}


@media (max-width: 426px) {
    .app {
        &__title {
            font-size: 35px;
        }
        &__menu {
            &-lists {
                font-size: 30px;
            }
        }
    }
}